.container {
    position: fixed;
    left: 30%;
    top: 12%;
    z-index: 20;
    width: 40%;
    min-width: 500px;
    height: auto;
    min-height: 650px;
    max-height: 85%;
    background-color:#EDF5F9;
    border: solid 2px #707070;
    border-radius: 27px;   
}

.form{
    display: flex;
    min-height: fit-content;   
}



.editButton{
    cursor: pointer;
    /* width: 100%; */
    height: 100%;
    font-size: 100%;
    font-family: inherit;
    border: none;
    border-radius: 5px;
    background-color: inherit;
    font-weight: inherit;
    outline: none;
}
  
  

.text{
    font-family: 'Lato', sans-serif;
    font-size: 1.1em;
    text-align: center;
}

.leftSide{
    width: 50%;
    height: 100%;
    margin: 5%;
} 

 .rightSide{
    position: absolute;
    width: 35%;
    right: 0;    
    height: 100%;
    margin: 5%;
}

.inputTitles{  
    display: flex;
    align-items: center;
}

.pic1{    
   margin-right: 5%; 
}

 .firstRow{ 
    /* height: 10%; */
}

