        border: 1px solid #000000; 
        background-color: #E6E6E6;
        border: solid 1px #363338;
        border-radius: 10px;       
        /* display: flex;    */
        /* justify-content: space-between;  */
        padding: 4px;
        font-weight: normal;
        resize: none;
        margin-right: 1%;
        font-size: 1.2em;
        height: auto;
        min-height: 20px;
        max-height: 100px;
}

.progressInput{      
    max-width: 20em;
    height: 2em;
    height: auto;
    max-height: 5em;
    border: 1px solid #000000; 
    background-color: #E6E6E6;
    border: solid 1px #363338;
    border-radius: 10px;       
    /* display: flex;    */
    /* justify-content: space-between;  */
    padding: 4px;
    font-weight: normal;
    resize: none;
    margin-right: 1%;
    font-size: 1em;
}




.nameContainer{    
    width: 100%;
    display: flex;   
    text-align: center; 
    height: fit-content;
    max-height: 100px;
    overflow-y: scroll;    
    overflow-wrap:break-word;
}

.progressContainer{    
    width: 100%;
    display: flex;   
    text-align: center; 
    /* height: 50px; */
    overflow-y: scroll;    
    overflow-wrap:break-word;
}



.nameContainer::-webkit-scrollbar {
  display: none;
}


.nameContainer {
  -ms-overflow-style: none; 
  scrollbar-width: none; 
}

.textName{
    font-family: 'Lato', sans-serif;    
    font-size: 1.2em;
    /* text-align: center; */
    margin-right: 10%;
    cursor: pointer;
    text-align: left;
    overflow-wrap:break-word;
    width: 100%;
}



.secondRow{
    margin-top: 8%;
    height: 10%;
}

.secondRowProgress{
    margin-top: 15%;
    height: 10%;
    cursor: pointer;
}

.pic2{    
    margin-top: 3%;
    margin-right: 5%; 
}

.bar {    
    height: 5%;
    width: 120px;
    border-radius: 4px;
    background-color: #E6E6E6;
    margin-top: 5px;
    margin-right: 10%;
}

.progress {
    height: 8px;
    border-radius: 4px;
}


.progressInputContainer{
    margin-top: 3%;
    width: 80%;
    display: flex;
    align-items: center;     
    text-align: center; 
}

.progressDiv{
    height: 30%;
    display: flex;
    align-items: center;
    margin-top: 5%;
    cursor: pointer;
}



.membersDiv{
    /* margin-left: 30%; */
    margin-right: 5%;
    margin-right: 10%;
    min-height: 10%;
    height: auto;
    width: auto;
    text-align: center;        
    font-weight: normal;
}

.members{
    display: flex;
    justify-content: flex-end;
    flex-wrap: nowrap;
    height: 65px;
    width: 100%;
    /* overflow-x: scroll; */
}

.dueDate{
    margin-top: 2%;
    margin-left: 30%;
    align-self: center;
}

.thirdRow{
    margin-top: 10%;
    height: 25%;
}

.fourthRow{
    margin-top: 8%;
    height: 20%;
}

.descriptinTitle{
    width: 20%;
    margin-bottom: 2%;
}

.descriptionInput{  
    width: 15em;
    height: 8em;
    border: 1px solid #000000; 
    background-color: #E6E6E6;
    border: solid 1px #363338;
    border-radius: 10px;       
    /* display: flex;    */
    /* justify-content: space-between;  */
    padding: 4px;
    font-weight: normal;
    resize: none;
}

.history{  
    width: 15em;
    height: 8em;
    border: 1px solid #000000; 
    background-color: #E6E6E6;
    border: solid 1px #363338;
    border-radius: 10px;       
    /* display: flex;    */
    /* justify-content: space-between;  */
    padding: 4px;
    font-weight: normal;
    overflow-y: auto;
    -ms-overflow-style: none;
    scrollbar-width: none; 
}

.descriptionButtons{
    height: 10%;
    /* width: 30%; */
    margin-left: 38%;
    margin-right: 35%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.lasRow{
    margin-top: 15%;
    display: flex;
    height: auto;
}

.lasRowRight{
    margin-top: 10%;
    height: 30%;
}


.smallButtons{
    display: flex;
    margin: 2%;
    margin-left: 20%;
    cursor: pointer;
    width: 60%;
    height: 22px;
    font-size: 80%;
    align-items: center;
    font-family: inherit;
    border: none;
    border-radius: 8px;
    background-color: #E6E6E6;
    font-weight: inherit;
    outline: none;
}

.picsSmallButtons{   
    /* margin-top:4%; */
    margin-left: 5%;
    margin-right: 5%; 
 }

 .smallButtonsNoPic{
    display: flex;
    margin: 2%;
    margin-left: 20%;
    cursor: pointer;
    width: 60%;
    height: 10%;
    font-size: 80%;
    align-items: center;
    justify-content: center;
    /* text-align: center; */
    font-family: inherit;
    border: none;
    border-radius: 8px;
    background-color: #E6E6E6;
    font-weight: inherit;
    outline: none;
}

.smallButtonsContainer{
    margin-top: 5%;
    height: 30%;
    margin-bottom: 10%;
}

