.card{
    border: 1px solid #000000; 
    background-color: #FFFFFF;
    border-radius: 10px;
    margin: 10px;   
    display: flex;   
    justify-content: space-between; 
    padding: 6px;
    font-weight: normal;
    height: fit-content;
    min-height: 35px;
    max-height: 200px; 
}

.leftSide{
    width: 68%;
    height: fit-content;
    overflow-x: hidden;    
}

.cardName{
    max-height: 180px;
    overflow-wrap:break-word;
    overflow-y: scroll;  
}

/* Hide scrollbar for Chrome, Safari and Opera */
.cardName::-webkit-scrollbar {
    display: none;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
  .cardName {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }

.bar {
    height: 8px;
    width: 120px;
    border-radius: 4px;
    background-color: #E6E6E6;
    margin-top: 5px;
}

.progress {
    height: 8px;
    border-radius: 4px;
}

.flex {
    display: flex;
    justify-content: flex-end;
    width: 32%;
    max-height: 30px; 
}

.pen {
    width: 30px;
    border-radius: 5px;
    margin-left: 5px;
}

.pen:hover {
    background-color: #C1CBCF;
}

